import React from "react";
import { useDispatch } from "react-redux";
import styled from "styled-components";
import { modalClose } from "../store/ticketModalToggleSlice";
import { useNavigate } from "react-router-dom";

function ConfirmModal() {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  return (
    <ConfirmAlert>
      <div className="bgBox"> </div>
      <div className="confirmBox">
        <h2> </h2>
        <ul className="cancelInfo">
          <li>
            <p></p>
            <span></span>
          </li>
          <li>
            <p>
                  2  <br />
              (,    1    )
            </p>
            <span></span>
          </li>
          <li>
            <p>
                 1    1   <br />
              (,    1    )
            </p>
            <span> 5%</span>
          </li>
          <li>
            <p>
                  1   <br />
              (,    1    )
            </p>
            <span> 10%</span>
          </li>
          <li>
            <p>       </p>
            <span> 30%</span>
          </li>
          <li>
            <p>   :   </p>
            <span> 100% </span>
          </li>
          <li>
            <p>   :   </p>
            <span> 100% </span>
          </li>
        </ul>
        <div className="subTxt">
          <ul>
            <li>
                  1   
               1      [
              <strong> </strong>]  .
            </li>
            <li>
              1    6.(   
              10 )
            </li>
            <li>
               (, )     
                      
              .
            </li>
            <li>
                    10   
              .
            </li>
            <li>
                      
                .
            </li>
            <li>   (  ) .</li>
            <li>
                       
               .
            </li>
          </ul>
        </div>
        <div className="btnBox">
          <div
            onClick={() => {
              dispatch(modalClose());
            }}
          >
            
          </div>
          <div
            onClick={() => {
              dispatch(modalClose());
              navigate(`/routeInfo`);
            }}
          >
            
          </div>
        </div>
      </div>
    </ConfirmAlert>
  );
}

const ConfirmAlert = styled.div`
  height: 100vh;
  width: 100%;
  z-index: 99;
  position: fixed;
  top: 0;
  left: 0;
  .bgBox {
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.4);
    text-indent: -9999px;
  }
  .confirmBox {
    width: 700px;
    height: 720px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    z-index: 999;
    h2 {
      background-color: #68b3ce;
      color: #fff;
      font-size: 22px;
      font-weight: normal;
      text-align: center;
      padding: 8px 0;
      height: 30px;
      line-height: 30px;
    }
    .cancelInfo {
      padding: 25px 30px 10px;
      li {
        border-bottom: var(--grey-border);
        font-size: 14px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        &:first-child {
          color: var(--blue-color);
          font-size: 13px;
          height: 30px;
          padding: 0 10px;
          border-top: 1px solid #aaa;
        }
        p {
          line-height: 1.5;
          padding: 9px 10px 9px 0;
          width: 380px;
        }
        span {
          width: 190px;
        }
      }
    }
    .subTxt {
      font-size: 12px;
      letter-spacing: -0.5px;
      color: #666;
      padding: 0 40px;
      ul {
        li {
          position: relative;
          line-height: 1.5;
          margin: 5px 0;
          strong {
            color: #5754b5;
            font-weight: normal;
          }
          &::before {
            content: "";
            display: block;
            width: 2px;
            height: 2px;
            border-radius: 50%;
            position: absolute;
            top: 6px;
            left: -8px;
            transform: translateY(-50%);
            background-color: #666;
          }
        }
      }
    }
    .btnBox {
      display: flex;
      gap: 10px;
      justify-content: center;
      align-items: center;
      margin-top: 25px;
      div {
        padding: 12px 20px;
        width: 90px;
        height: 25px;
        background-color: #b8becc;
        color: #fff;
        line-height: 25px;
        text-align: center;
        cursor: pointer;
        &:nth-child(2) {
          background-color: #5754b5;
        }
      }
    }
  }
`;

export default ConfirmModal;
