import { createSlice } from "@reduxjs/toolkit";

const getGrade = createSlice({
  name: "setGrade",
  initialState: {
    id: 0,
    data: "",
  },
  reducers: {
    setGrade: (state, action) => {
      state.id = action.payload;
      if (state.id === 1) {
        state.data = "";
      } else if (state.id === 5) {
        state.data = "";
      } else if (state.id === 7) {
        state.data = "";
      } else {
        state.data = "";
      }
    },
  },
});

export const { setGrade } = getGrade.actions;
export default getGrade.reducer;
